.nav {
    grid-area: n;
    background-color: #E5E5E5;
    padding: 20px 0px 0px 20px;
    font-weight: bold;


}
.items{
    background-color: #FFF;
    padding: 20px 20px;
    border-radius: 15px;
    margin-top: 0px;
}
.item{

    border-radius: 15px;
    list-style: none;
    transition: background-color .15s;
    box-sizing: border-box;


}
.item:hover{
    background-color: #F4F4F4;
    width: 100%;
}
.item a{
    color: #181818;
    text-decoration: none;
    width: 100%;
    height: 100%;
    display: inline-block;
    padding: 15px 15px;
}
.item a.active{
    color:#0077B5;
}
@media screen and (max-width: 620px){
    .nav{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        padding: 10px 0px 0px 10px;
    }
    .item:hover{
        background-color: #FFFFFF;
        width: fit-content;
    }
    .item a{
        padding: 10px 10px;
        width: fit-content;

    }
    .items{
        display: flex;
        flex-wrap: wrap;
        margin-right: 10px;
        width: 100%;
        justify-content: space-evenly;
        padding: 20px 0px;
    }
}
